<template>
	<div>
		<h1>{{ id ? '编辑' : '新建' }}英雄</h1>
		<el-form label-width="80px" @submit.native.prevent="save">
			<el-tabs value="basic" type="border-card">
				<el-tab-pane label="基本信息" name="basic">
					<el-form-item label="名称"><el-input v-model="model.name"></el-input></el-form-item>
					<el-form-item label="头像">
						<el-upload
							class="avatar-uploader"
							:headers="getAuthHeaders()"
							:action="uploadUrl"
							:show-file-list="false"
							:on-success="res => $set(model, 'avatar', res.url)"
						>
							<img v-if="model.avatar" :src="model.avatar" class="avatar" />
							<i v-else class="el-icon-plus avatar-uploader-icon"></i>
						</el-upload>
					</el-form-item>
					<el-form-item label="背景">
						<el-upload
							class="avatar-uploader"
							:headers="getAuthHeaders()"
							:action="uploadUrl"
							:show-file-list="false"
							:on-success="res => $set(model, 'banner', res.url)"
						>
							<img v-if="model.banner" :src="model.banner" class="avatar" />
							<i v-else class="el-icon-plus avatar-uploader-icon"></i>
						</el-upload>
					</el-form-item>
					<el-form-item label="称号"><el-input v-model="model.title"></el-input></el-form-item>
					<el-form-item label="类型">
						<el-select v-model="model.categories" multiple>
							<el-option v-for="item in categories" :key="item._id" :label="item.name" :value="item._id"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="难度"><el-rate style="margin-top: 0.6rem;" :max="9" show-score v-model="model.scores.difficult"></el-rate></el-form-item>
					<el-form-item label="技能"><el-rate style="margin-top: 0.6rem;" :max="9" show-score v-model="model.scores.skills"></el-rate></el-form-item>
					<el-form-item label="攻击"><el-rate style="margin-top: 0.6rem;" :max="9" show-score v-model="model.scores.attack"></el-rate></el-form-item>
					<el-form-item label="生存"><el-rate style="margin-top: 0.6rem;" :max="9" show-score v-model="model.scores.survive"></el-rate></el-form-item>
					<el-form-item label="顺风出装">
						<el-select v-model="model.items1" multiple><el-option v-for="item in items" :key="item._id" :label="item.name" :value="item._id"></el-option></el-select>
					</el-form-item>
					<el-form-item label="逆风出装">
						<el-select v-model="model.items2" multiple><el-option v-for="item in items" :key="item._id" :label="item.name" :value="item._id"></el-option></el-select>
					</el-form-item>
					<el-form-item label="使用技巧"><el-input type="textarea" v-model="model.usageTips"></el-input></el-form-item>
					<el-form-item label="对抗技巧"><el-input type="textarea" v-model="model.battleTips"></el-input></el-form-item>
					<el-form-item label="团战思路"><el-input type="textarea" v-model="model.teamTips"></el-input></el-form-item>
				</el-tab-pane>
				<el-tab-pane label="技能" name="skills">
					<el-button size="small" @click="model.skills.push({})">
						<i class="el-icon-plus"></i>
						添加技能
					</el-button>
					<el-row type="flex" style="flex-wrap:wrap;margin-top: 1rem;">
						<el-col :md="12" v-for="(item, i) in model.skills" :key="i">
							<el-form-item label="名称"><el-input v-model="item.name"></el-input></el-form-item>
							<el-form-item label="冷却值"><el-input v-model="model.delay"></el-input></el-form-item>
							<el-form-item label="消耗"><el-input v-model="model.cost"></el-input></el-form-item>
							<el-form-item label="图标">
								<el-upload
									class="avatar-uploader"
									:headers="getAuthHeaders()"
									:action="uploadUrl"
									:show-file-list="false"
									:on-success="res => $set(item, 'icon', res.url)"
								>
									<img v-if="item.icon" :src="item.icon" class="avatar" />
									<i v-else class="el-icon-plus avatar-uploader-icon"></i>
								</el-upload>
							</el-form-item>
							<el-form-item label="描述"><el-input type="textarea" v-model="item.description"></el-input></el-form-item>
							<el-form-item label="小提示"><el-input type="textarea" v-model="item.tips"></el-input></el-form-item>
							<el-form-item style="margin-top: 1rem;"><el-button type="danger" @click="model.skills.splice(i, 1)">删除</el-button></el-form-item>
						</el-col>
					</el-row>
				</el-tab-pane>
				<el-tab-pane label="拍档" name="partners">
					<el-button size="small" @click="model.partners.push({})">
						<i class="el-icon-plus"></i>
						添加拍档
					</el-button>
					<el-row type="flex" style="flex-wrap:wrap;margin-top: 1rem;">
						<el-col :md="12" v-for="(item, i) in model.partners" :key="i">
							<el-form-item label="英雄名称">
								<el-select filterable v-model="item.hero">
									<el-option v-for="i in heroes" :value="i._id" :label="i.name" :key="i._id"></el-option>
								</el-select>
								</el-form-item>
							<el-form-item label="描述"><el-input type="textarea" v-model="item.description"></el-input></el-form-item>
							<el-form-item style="margin-top: 1rem;"><el-button type="danger" @click="model.partners.splice(i, 1)">删除</el-button></el-form-item>
						</el-col>
					</el-row>
				</el-tab-pane>
			</el-tabs>
			<el-form-item style="margin-top: 1rem;"><el-button type="primary" native-type="submit">保存</el-button></el-form-item>
		</el-form>
	</div>
</template>

<script>
export default {
	props: {
		id: {}
	},
	data() {
		return {
			model: {
				scores: {
					difficult: 0,
					skills: 0,
					attack: 0,
					survive: 0
				},
				avatar: '',
				name: '',
				skills: [],
				partners: []
			},
			categories: [],
			items: [],
			heroes:[]
		};
	},
	methods: {
		afterUpload(res) {
			this.$set(this.model, 'avatar', res.url);
		},

		async save() {
			if (this.id) {
				await this.$http.put(`rest/heroes/${this.id}`, this.model);
			} else {
				await this.$http.post('rest/heroes', this.model);
			}

			this.$router.push('/heroes/list');
			this.$message({
				type: 'success',
				message: '保存成功'
			});
		},
		async fetch() {
			const res = await this.$http.get(`rest/heroes/${this.id}`);
			this.model = Object.assign({}, this.model, res.data);
		},
		async fetchCategories() {
			const res = await this.$http.get(`rest/categories`);
			this.categories = res.data;
		},
		async fetchItems() {
			const res = await this.$http.get(`rest/items`);
			this.items = res.data;
		},
		async fetchHeroes() {
			const res = await this.$http.get(`rest/heroes`);
			this.heroes = res.data;
		}
	},
	created() {
		this.fetchCategories();
		this.fetchItems();
		this.fetchHeroes();
		this.id && this.fetch();
	}
};
</script>

<style></style>
